/* Container Styles */
.container {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 2rem;
    padding-bottom: 2rem;
}

/* Featured Section */
.featured-container {
    margin-bottom: 2rem;
}

.featured-badge {
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
}

.featured-indicator {
    height: 2.5rem;
    width: 1.25rem;
    border-radius: 0.25rem;
    background-color: #db4444;
}

.featured-text {
    color: #db4444;
    font-weight: 600;
}

.section-title {
    font-size: 2.25rem;
    font-weight: 700;
    margin-top: 1rem;
    margin-bottom: 2rem;
}

/* New Arrival Grid Layout */
.new-arrival-grid {
    display: grid;
    grid-template-columns: 2fr 1.5fr;
    gap: 1.5rem;
}

.large-card {
    position: relative;
    border-radius: 12px;
    overflow: hidden;
    min-height: 420px;
    height: 100%;
    background: #111;
    display: flex;
    align-items: stretch;
}

.new-arrival-right-grid {
    display: grid;
    grid-template-rows: 1fr 1fr;
    gap: 1.5rem;
    height: 100%;
}

.women-card {
    position: relative;
    border-radius: 12px;
    overflow: hidden;
    min-height: 200px;
    background: #111;
    display: flex;
    align-items: stretch;
}

.new-arrival-bottom-row {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.5rem;
    height: 100%;
}

.speakers-card, .perfume-card {
    position: relative;
    border-radius: 12px;
    overflow: hidden;
    min-height: 200px;
    background: #222;
    display: flex;
    align-items: stretch;
}

.new-arrival-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
}

.overlay {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba(0,0,0,0.45) 60%, rgba(0,0,0,0.85) 100%);
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 2rem;
    box-sizing: border-box;
}

.product-title-large {
    font-size: 2rem;
    font-weight: 700;
    margin-bottom: 0.5rem;
}

.product-title {
    font-size: 1.25rem;
    font-weight: 700;
    margin-bottom: 0.25rem;
}

.product-description {
    margin-bottom: 1rem;
    font-size: 1rem;
    color: #fff;
    opacity: 0.9;
}

.shop-link {
    display: inline-block;
    border-bottom: 2px solid #fff;
    font-weight: 600;
    padding-bottom: 0.25rem;
    color: #fff;
    text-decoration: none;
    font-size: 1rem;
    margin-top: 0.5rem;
    transition: color 0.2s, border-color 0.2s;
}
.shop-link:hover {
    color: #db4444;
    border-color: #db4444;
}
.shop-link-small {
    font-size: 0.95rem;
}

/* Enhanced Responsive Styles */
@media (max-width: 1024px) {
    .container {
        padding: 1.5rem 1rem;
    }

    .section-title {
        font-size: 2rem;
        margin-top: 0.75rem;
        margin-bottom: 1.5rem;
    }

    .new-arrival-grid {
        grid-template-columns: 1fr;
        gap: 1.25rem;
    }

    .new-arrival-right-grid {
        grid-template-rows: 1fr 1fr;
        gap: 1.25rem;
    }

    .new-arrival-bottom-row {
        grid-template-columns: 1fr 1fr;
        gap: 1.25rem;
    }

    .large-card {
        min-height: 360px;
    }

    .women-card,
    .speakers-card,
    .perfume-card {
        min-height: 180px;
    }

    .overlay {
        padding: 1.5rem;
    }

    .product-title-large {
        font-size: 1.75rem;
    }

    .product-title {
        font-size: 1.1rem;
    }

    .product-description {
        font-size: 0.95rem;
    }
}

@media (max-width: 768px) {
    .container {
        padding: 1.25rem 0.75rem;
    }

    .section-title {
        font-size: 1.75rem;
        margin-top: 0.5rem;
        margin-bottom: 1.25rem;
    }

    .new-arrival-grid {
        grid-template-columns: 1fr;
        gap: 1rem;
    }

    .new-arrival-right-grid {
        grid-template-rows: 1fr 1fr;
        gap: 1rem;
    }

    .new-arrival-bottom-row {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .large-card {
        min-height: 300px;
    }

    .women-card,
    .speakers-card,
    .perfume-card {
        min-height: 160px;
    }

    .overlay {
        padding: 1.25rem;
    }

    .product-title-large {
        font-size: 1.5rem;
    }

    .product-title {
        font-size: 1rem;
    }

    .product-description {
        font-size: 0.9rem;
        margin-bottom: 0.75rem;
    }

    .shop-link {
        font-size: 0.95rem;
    }
}

@media (max-width: 480px) {
    .container {
        padding: 1rem 0.5rem;
    }

    .section-title {
        font-size: 1.5rem;
        margin-top: 0.5rem;
        margin-bottom: 1rem;
    }

    .new-arrival-grid,
    .new-arrival-right-grid,
    .new-arrival-bottom-row {
        grid-template-columns: 1fr;
        grid-template-rows: unset;
        gap: 0.75rem;
    }

    .large-card {
        min-height: 240px;
    }

    .women-card,
    .speakers-card,
    .perfume-card {
        min-height: 140px;
    }

    .overlay {
        padding: 1rem;
    }

    .product-title-large {
        font-size: 1.25rem;
        margin-bottom: 0.25rem;
    }

    .product-title {
        font-size: 0.95rem;
        margin-bottom: 0.15rem;
    }

    .product-description {
        font-size: 0.85rem;
        margin-bottom: 0.5rem;
    }

    .shop-link {
        font-size: 0.9rem;
        padding-bottom: 0.15rem;
    }

    .shop-link-small {
        font-size: 0.85rem;
    }
} 