.contact-page {
  background: #fafafa;
  min-height: 100vh;
  padding-bottom: 40px;
}
.breadcrumb {
  padding: 32px 40px 0 40px;
  color: #b3b3b3;
  font-size: 15px;
  margin-bottom: 16px;
  font-family: 'Poppins', Arial, sans-serif;
}
.breadcrumb .active {
  color: #222;
  font-weight: 500;
}
.contact-main {
  display: flex;
  gap: 40px;
  padding: 0 40px;
  margin-bottom: 48px;
  justify-content: flex-start;
  align-items: stretch;
}
.contact-info-box {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 16px rgba(0,0,0,0.04);
  padding: 32px 32px 32px 32px;
  min-width: 340px;
  max-width: 370px;
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 24px;
  height: 100%;
  justify-content: flex-start;
}
.contact-info-item {
  display: flex;
  align-items: flex-start;
  gap: 16px;
  margin-bottom: 12px;
}
.contact-info-icon {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background: none;
  flex-shrink: 0;
}
.contact-info-title {
  font-size: 17px;
  font-weight: 600;
  color: #222;
  margin-bottom: 4px;
  font-family: 'Poppins', Arial, sans-serif;
}
.contact-info-desc {
  font-size: 15px;
  color: #222;
  margin-bottom: 2px;
  font-family: 'Poppins', Arial, sans-serif;
}
.contact-info-box hr {
  border: none;
  border-top: 1px solid #e5e5e5;
  margin: 18px 0 18px 0;
}
.contact-form {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 16px rgba(0,0,0,0.04);
  padding: 32px 32px 32px 32px;
  min-width: 400px;
  max-width: 700px;
  flex: 2;
  display: flex;
  flex-direction: column;
  gap: 18px;
  justify-content: flex-start;
}
.contact-form-row {
  display: flex;
  gap: 16px;
  margin-bottom: 18px;
}
.contact-form input[type="text"],
.contact-form input[type="email"] {
  flex: 1;
  padding: 16px 14px;
  border: none;
  border-radius: 6px;
  font-size: 15px;
  outline: none;
  background: #f5f5f5;
  font-family: 'Poppins', Arial, sans-serif;
  color: #222;
  box-shadow: none;
}
.contact-form input[type="text"]::placeholder,
.contact-form input[type="email"]::placeholder {
  color: #b3b3b3;
  font-size: 15px;
}
.contact-form textarea {
  padding: 18px 14px;
  border: none;
  border-radius: 6px;
  font-size: 15px;
  outline: none;
  background: #f5f5f5;
  min-height: 140px;
  resize: none;
  font-family: 'Poppins', Arial, sans-serif;
  color: #222;
  margin-bottom: 18px;
}
.contact-form textarea::placeholder {
  color: #b3b3b3;
  font-size: 15px;
}
.send-btn {
  background: #db4444;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 14px 0;
  font-size: 17px;
  font-weight: 500;
  width: 100%;
  align-self: flex-end;
  cursor: pointer;
  margin-top: 8px;
  font-family: 'Poppins', Arial, sans-serif;
  transition: background 0.2s;
}
.send-btn:hover {
  background: #b83232;
}

/* Enhanced Responsive Styles */
@media (max-width: 1024px) {
  .breadcrumb {
    padding: 24px 24px 0 24px;
  }

  .contact-main {
    gap: 32px;
    padding: 0 24px;
  }

  .contact-info-box {
    padding: 24px 24px 24px 24px;
    min-width: 300px;
  }

  .contact-info-title {
    font-size: 16px;
  }

  .contact-info-desc {
    font-size: 14px;
  }

  .contact-form {
    padding: 24px 24px 24px 24px;
    min-width: 360px;
  }

  .contact-form input[type="text"],
  .contact-form input[type="email"] {
    padding: 14px 12px;
    font-size: 14px;
  }

  .contact-form textarea {
    padding: 16px 12px;
    font-size: 14px;
    min-height: 120px;
  }
}

@media (max-width: 768px) {
  .breadcrumb {
    padding: 20px 16px 0 16px;
    font-size: 14px;
  }

  .contact-main {
    flex-direction: column;
    gap: 24px;
    padding: 0 16px;
  }

  .contact-info-box {
    width: 100%;
    max-width: none;
    min-width: unset;
    padding: 20px 20px 20px 20px;
  }

  .contact-info-item {
    gap: 12px;
    margin-bottom: 10px;
  }

  .contact-info-icon {
    width: 36px;
    height: 36px;
  }

  .contact-info-title {
    font-size: 15px;
  }

  .contact-info-desc {
    font-size: 13px;
  }

  .contact-info-box hr {
    margin: 14px 0 14px 0;
  }

  .contact-form {
    width: 100%;
    min-width: unset;
    padding: 20px 20px 20px 20px;
  }

  .contact-form-row {
    flex-direction: column;
    gap: 12px;
    margin-bottom: 12px;
  }

  .contact-form input[type="text"],
  .contact-form input[type="email"] {
    padding: 12px;
    font-size: 13px;
  }

  .contact-form textarea {
    padding: 14px 12px;
    font-size: 13px;
    min-height: 100px;
    margin-bottom: 14px;
  }

  .send-btn {
    padding: 12px 0;
    font-size: 15px;
  }
}

@media (max-width: 480px) {
  .breadcrumb {
    padding: 16px 12px 0 12px;
    font-size: 13px;
  }

  .contact-main {
    gap: 20px;
    padding: 0 12px;
  }

  .contact-info-box {
    padding: 16px 16px 16px 16px;
  }

  .contact-info-item {
    gap: 10px;
    margin-bottom: 8px;
  }

  .contact-info-icon {
    width: 32px;
    height: 32px;
  }

  .contact-info-title {
    font-size: 14px;
  }

  .contact-info-desc {
    font-size: 12px;
  }

  .contact-info-box hr {
    margin: 12px 0 12px 0;
  }

  .contact-form {
    padding: 16px 16px 16px 16px;
  }

  .contact-form input[type="text"],
  .contact-form input[type="email"] {
    padding: 10px;
    font-size: 12px;
  }

  .contact-form textarea {
    padding: 12px 10px;
    font-size: 12px;
    min-height: 80px;
    margin-bottom: 12px;
  }

  .send-btn {
    padding: 10px 0;
    font-size: 14px;
  }
} 