.auth-container {
  display: flex;
  justify-content: center;
  align-items: stretch;
  min-height: 70vh;
  background: #fafafa;
  padding: 48px 0;
}
.auth-image {
  flex: 1;
  display: flex;
  align-items: stretch;
  justify-content: center;
  min-width: 350px;
  max-width: 600px;
  margin-right: 48px;
  height: auto;
}
.auth-image img {
  width: 100%;
  height: 100%;
  max-width: 100%;
  object-fit: cover;
  border-radius: 0;
  background: none;
  padding: 0;
  display: block;
}
.auth-form {
  flex: 1;
  max-width: 400px;
  background: #fff;
  padding: 40px 32px;
  border-radius: 12px;
  box-shadow: 0 2px 16px rgba(0,0,0,0.04);
  display: flex;
  flex-direction: column;
  gap: 18px;
  height: 100%;
}
.auth-form h2 {
  font-size: 2rem;
  margin-bottom: 8px;
}
.auth-form p {
  color: #888;
  margin-bottom: 16px;
}
.auth-form input {
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 16px;
  margin-bottom: 8px;
}
.auth-main-btn {
  background: #db4444;
  color: #fff;
  border: none;
  padding: 12px;
  border-radius: 6px;
  font-size: 17px;
  font-weight: 500;
  margin-bottom: 8px;
  cursor: pointer;
}
.auth-google-btn {
  background: #fff;
  color: #222;
  border: 1px solid #ddd;
  padding: 12px;
  border-radius: 6px;
  font-size: 17px;
  font-weight: 500;
  margin-bottom: 8px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}
.auth-link-row {
  text-align: center;
  color: #888;
  font-size: 15px;
}
.auth-link-row a {
  color: #222;
  text-decoration: underline;
  margin-left: 4px;
}
.auth-btn-row {
  display: flex;
  align-items: center;
  gap: 24px;
}
.auth-forgot {
  color: #db4444;
  font-size: 15px;
  text-decoration: underline;
  margin-left: 8px;
}
.login-form {
  align-items: flex-start;
  padding: 40px 32px 32px 32px;
}
.auth-title {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 4px;
  text-align: left;
}
.auth-subtitle {
  color: #222;
  font-size: 14px;
  margin-bottom: 18px;
  text-align: left;
}
.auth-input {
  width: 100%;
  border: none;
  border-bottom: 1.5px solid #ddd;
  background: transparent;
  padding: 12px 0 8px 0;
  margin-bottom: 18px;
  font-size: 16px;
  color: #222;
  outline: none;
  font-family: 'Poppins', Arial, sans-serif;
}
.login-btn-row {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  margin-top: 12px;
}
.login-btn {
  width: 160px;
  background: #db4444;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 12px 0;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  margin-right: 16px;
}
.login-forgot {
  color: #db4444;
  font-size: 15px;
  text-decoration: none;
  margin-left: 16px;
  white-space: nowrap;
}
.signup-form {
  align-items: flex-start;
  padding: 40px 32px 32px 32px;
}
.signup-btn {
  width: 100%;
  background: #db4444;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 14px 0;
  font-size: 17px;
  font-weight: 500;
  cursor: pointer;
  margin-bottom: 16px;
  margin-top: 8px;
}
.auth-google-btn {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1.5px solid #ddd;
  background: #fff;
  color: #222;
  border-radius: 6px;
  padding: 12px 0;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  margin-bottom: 24px;
  transition: border 0.2s;
}
.auth-google-btn:hover {
  border: 1.5px solid #bbb;
}
.google-icon {
  display: flex;
  align-items: center;
  margin-right: 10px;
}
.signup-link-row {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  color: #222;
  margin-top: 8px;
}
.login-link {
  color: #222;
  font-weight: 500;
  margin-left: 6px;
  text-decoration: underline #bbb 2px;
  text-underline-offset: 3px;
  transition: color 0.2s;
}
.login-link:hover {
  color: #db4444;
  text-decoration: underline #db4444 2px;
}

/* Tablet styles */
@media (max-width: 1024px) {
  .auth-container {
    padding: 32px 0;
  }
  
  .auth-image {
    min-width: 300px;
    margin-right: 32px;
  }
  
  .auth-form {
    padding: 32px 24px;
  }
  
  .auth-form h2 {
    font-size: 1.75rem;
  }
  
  .auth-title {
    font-size: 1.75rem;
  }
  
  .auth-input {
    font-size: 15px;
  }
  
  .login-btn {
    width: 140px;
    font-size: 15px;
  }
  
  .signup-btn {
    font-size: 16px;
  }
}

/* Mobile styles */
@media (max-width: 768px) {
  .auth-container {
    flex-direction: column;
    padding: 24px 16px;
    min-height: unset;
  }
  
  .auth-image {
    display: none;
  }
  
  .auth-form {
    max-width: 100%;
    padding: 24px 20px;
  }
  
  .auth-form h2 {
    font-size: 1.5rem;
  }
  
  .auth-title {
    font-size: 1.5rem;
  }
  
  .auth-subtitle {
    font-size: 13px;
  }
  
  .auth-input {
    font-size: 14px;
    padding: 10px 0 6px 0;
    margin-bottom: 16px;
  }
  
  .login-btn-row {
    flex-direction: column;
    gap: 12px;
    align-items: flex-start;
  }
  
  .login-btn {
    width: 100%;
    margin-right: 0;
  }
  
  .login-forgot {
    margin-left: 0;
  }
  
  .signup-btn,
  .auth-google-btn {
    font-size: 15px;
    padding: 12px 0;
  }
  
  .signup-link-row {
    font-size: 14px;
  }
}

/* Small mobile styles */
@media (max-width: 480px) {
  .auth-container {
    padding: 16px 12px;
  }
  
  .auth-form {
    padding: 20px 16px;
  }
  
  .auth-form h2 {
    font-size: 1.25rem;
  }
  
  .auth-title {
    font-size: 1.25rem;
  }
  
  .auth-subtitle {
    font-size: 12px;
  }
  
  .auth-input {
    font-size: 13px;
    padding: 8px 0 4px 0;
    margin-bottom: 12px;
  }
  
  .login-btn,
  .signup-btn,
  .auth-google-btn {
    font-size: 14px;
    padding: 10px 0;
  }
  
  .signup-link-row {
    font-size: 13px;
  }
} 