.account-page {
  background: #fafafa;
  min-height: 100vh;
  padding-bottom: 40px;
}
.breadcrumb {
  padding: 32px 40px 0 40px;
  color: #b3b3b3;
  font-size: 15px;
  margin-bottom: 16px;
  font-family: 'Poppins', Arial, sans-serif;
}
.breadcrumb .active {
  color: #222;
  font-weight: 500;
}
.account-header {
  font-size: 15px;
  color: #222;
  margin-bottom: 0;
  text-align: right;
  padding: 0 60px 18px 0;
  font-family: 'Poppins', Arial, sans-serif;
}
.account-username {
  color: #db4444;
  font-weight: 500;
}
.account-main {
  display: flex;
  gap: 40px;
  padding: 0 40px;
  margin-bottom: 48px;
  justify-content: flex-start;
  align-items: flex-start;
}
.account-sidebar {
  min-width: 210px;
  max-width: 230px;
  padding-top: 32px;
  font-family: 'Poppins', Arial, sans-serif;
}
.sidebar-section {
  margin-bottom: 38px;
}
.sidebar-title {
  font-size: 16px;
  color: #222;
  margin-bottom: 10px;
  font-weight: 600;
}
.sidebar-link {
  font-size: 15px;
  color: #888;
  margin-bottom: 8px;
  cursor: pointer;
  padding: 4px 0 4px 8px;
  border-left: 3px solid transparent;
  display: block;
  transition: color 0.2s, border-color 0.2s;
  font-weight: 400;
}
.sidebar-link.active {
  color: #db4444;
  border-left: 3px solid #db4444;
  font-weight: 600;
  background: none;
}
.sidebar-link.disabled {
  color: #b3b3b3;
  cursor: not-allowed;
  font-weight: 400;
}
.account-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 0;
}
.profile-form {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 16px rgba(0,0,0,0.04);
  padding: 40px 48px 32px 48px;
  max-width: 700px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 18px;
  font-family: 'Poppins', Arial, sans-serif;
}
.profile-title {
  color: #db4444;
  font-size: 1.35rem;
  font-weight: 600;
  margin-bottom: 18px;
}
.profile-row {
  display: flex;
  gap: 24px;
  margin-bottom: 8px;
}
.profile-row label {
  flex: 1;
  font-size: 15px;
  color: #222;
  display: flex;
  flex-direction: column;
  gap: 6px;
  font-weight: 500;
}
.profile-form input[type="text"],
.profile-form input[type="email"],
.profile-form input[type="password"] {
  padding: 14px 14px;
  border: none;
  border-radius: 6px;
  font-size: 15px;
  outline: none;
  background: #f5f5f5;
  font-family: 'Poppins', Arial, sans-serif;
  color: #222;
  margin-bottom: 0;
}
.profile-form input[type="text"]::placeholder,
.profile-form input[type="email"]::placeholder,
.profile-form input[type="password"]::placeholder {
  color: #b3b3b3;
  font-size: 15px;
}
.profile-section-title {
  font-size: 15px;
  color: #888;
  margin: 18px 0 8px 0;
  font-weight: 600;
}
.profile-actions {
  display: flex;
  justify-content: flex-end;
  gap: 18px;
  margin-top: 18px;
}
.cancel-btn {
  background: none;
  border: none;
  color: #222;
  font-size: 15px;
  cursor: pointer;
  padding: 10px 18px;
  font-family: 'Poppins', Arial, sans-serif;
  font-weight: 500;
  transition: color 0.2s;
}
.cancel-btn:hover {
  color: #db4444;
}
.save-btn {
  background: #db4444;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 14px 38px;
  font-size: 17px;
  font-weight: 500;
  cursor: pointer;
  font-family: 'Poppins', Arial, sans-serif;
  transition: background 0.2s;
}
.save-btn:hover {
  background: #b83232;
}

/* Enhanced Responsive Styles */
@media (max-width: 1024px) {
  .breadcrumb {
    padding: 24px 24px 0 24px;
  }

  .account-header {
    padding: 0 24px 16px 0;
  }

  .account-main {
    gap: 32px;
    padding: 0 24px;
  }

  .account-sidebar {
    min-width: 180px;
  }

  .profile-form {
    padding: 32px 32px 24px 32px;
  }

  .profile-title {
    font-size: 1.25rem;
  }

  .profile-row {
    gap: 16px;
  }
}

@media (max-width: 768px) {
  .breadcrumb {
    padding: 20px 16px 0 16px;
    font-size: 14px;
  }

  .account-header {
    padding: 0 16px 14px 0;
    font-size: 14px;
  }

  .account-main {
    flex-direction: column;
    gap: 24px;
    padding: 0 16px;
  }

  .account-sidebar {
    width: 100%;
    max-width: none;
    padding-top: 0;
  }

  .sidebar-section {
    margin-bottom: 24px;
  }

  .sidebar-title {
    font-size: 15px;
  }

  .sidebar-link {
    font-size: 14px;
  }

  .profile-form {
    padding: 24px 20px 20px 20px;
  }

  .profile-title {
    font-size: 1.15rem;
    margin-bottom: 16px;
  }

  .profile-row {
    flex-direction: column;
    gap: 12px;
    margin-bottom: 12px;
  }

  .profile-row label {
    font-size: 14px;
  }

  .profile-form input[type="text"],
  .profile-form input[type="email"],
  .profile-form input[type="password"] {
    padding: 12px;
    font-size: 14px;
  }

  .profile-section-title {
    font-size: 14px;
    margin: 16px 0 6px 0;
  }

  .profile-actions {
    flex-direction: column;
    gap: 12px;
    margin-top: 16px;
  }

  .cancel-btn,
  .save-btn {
    width: 100%;
    text-align: center;
    font-size: 15px;
  }

  .save-btn {
    padding: 12px 24px;
  }
}

@media (max-width: 480px) {
  .breadcrumb {
    padding: 16px 12px 0 12px;
    font-size: 13px;
  }

  .account-header {
    padding: 0 12px 12px 0;
    font-size: 13px;
  }

  .account-main {
    gap: 20px;
    padding: 0 12px;
  }

  .sidebar-title {
    font-size: 14px;
  }

  .sidebar-link {
    font-size: 13px;
    padding: 3px 0 3px 6px;
  }

  .profile-form {
    padding: 20px 16px 16px 16px;
  }

  .profile-title {
    font-size: 1.1rem;
    margin-bottom: 14px;
  }

  .profile-row label {
    font-size: 13px;
  }

  .profile-form input[type="text"],
  .profile-form input[type="email"],
  .profile-form input[type="password"] {
    padding: 10px;
    font-size: 13px;
  }

  .profile-section-title {
    font-size: 13px;
    margin: 14px 0 4px 0;
  }

  .cancel-btn,
  .save-btn {
    font-size: 14px;
  }

  .save-btn {
    padding: 10px 20px;
  }
} 