.homepage {
  background: #fafafa;
  min-height: 100vh;
  padding-bottom: 40px;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 16px;
}

.section-header, .best-header, .category-header, .explore-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 24px;
  padding-top: 32px;
}

.section-badge, .category-badge {
  background: #db4444;
  color: #fff;
  border-radius: 4px;
  padding: 4px 12px;
  font-size: 14px;
  font-weight: 500;
}

h2, h3 {
  font-size: 2rem;
  font-weight: 700;
  margin: 0;
}

.card, .category-card, .product-card {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.04);
  padding: 16px;
  margin-bottom: 32px;
  transition: box-shadow 0.2s;
}

.card:hover, .category-card:hover, .product-card:hover {
  box-shadow: 0 4px 16px rgba(0,0,0,0.08);
}

.grid, .category-list, .product-list, .arrival-list, .explore-list {
  display: flex;
  flex-wrap: wrap;
  gap: 24px;
  justify-content: center;
}

.button, .view-all-btn, .hero-banner-buy-btn {
  background: #00ff6a;
  color: #111;
  border: none;
  border-radius: 6px;
  padding: 14px 38px;
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s;
}

.button:hover, .view-all-btn:hover, .hero-banner-buy-btn:hover {
  background: #00e65c;
}

/* Responsive adjustments */
@media (max-width: 1024px) {
  .container {
    padding: 0 24px;
  }
  .grid, .category-list, .product-list, .arrival-list, .explore-list {
    gap: 16px;
  }
}
@media (max-width: 768px) {
  .container {
    padding: 0 16px;
  }
  .section-header, .best-header, .category-header, .explore-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
    padding-top: 18px;
  }
  .grid, .category-list, .product-list, .arrival-list, .explore-list {
    flex-direction: column;
    align-items: center;
    gap: 12px;
  }
}

.banner-section {
  display: flex;
  gap: 16px;
  padding: 32px 40px 0 40px;
  align-items: flex-start;
}
.banner-menu {
  min-width: 220px;
  margin: 0;
  padding: 0;
}
.banner-menu ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.banner-menu li {
  padding: 10px 0 10px 0;
  font-size: 17px;
  color: #222;
  cursor: pointer;
  margin: 0;
  text-align: left;
}
.banner-main {
  flex: 1;
  display: flex;
  align-items: stretch;
  min-width: 0;
}
.banner-slider {
  position: relative;
  width: 100%;
  min-height: 340px;
  background: #eee;
  border-radius: 16px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.banner-slider img {
  width: 100%;
  max-height: 320px;
  object-fit: cover;
}
.banner-text {
  position: absolute;
  left: 40px;
  top: 40px;
  color: #fff;
  text-shadow: 0 2px 8px rgba(0,0,0,0.3);
}
.banner-text span {
  font-size: 18px;
}
.banner-text h2 {
  font-size: 2.5rem;
  margin: 0;
}
.section-header .flash-timer {
  margin-left: auto;
  font-size: 18px;
  color: #db4444;
  font-weight: bold;
}
.best-header .view-all-btn, .explore-header .view-all-btn, .category-header .view-all-btn {
  margin-left: auto;
  background: #db4444;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 10px 32px;
  font-size: 16px;
  cursor: pointer;
}
.flash-sales, .categories, .best-selling, .explore-products, .new-arrival {
  margin-bottom: 32px;
  padding: 0 40px;
}
.product-list, .category-list, .arrival-list, .best-selling .product-list {
  display: flex;
  gap: 24px;
  flex-wrap: wrap;
  justify-content: flex-start;
  padding: 24px 0 0 0;
}
.product-card, .category-card, .arrival-card, .explore-card {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.04);
  padding: 16px;
  width: 220px;
  min-height: 320px;
  display: flex;
  flex-direction: column;
  align-items: center;
