}
.product-card img, .category-card svg, .arrival-card img {
  width: 100%;
  max-width: 160px;
  border-radius: 8px;
  margin-bottom: 12px;
}
.product-info, .arrival-info, .explore-title, .explore-price, .explore-rating {
