/* Enhanced Responsive Styles */
@media (max-width: 1024px) {
  .container {
    padding: 0 24px;
  }
  
  .banner-section {
    padding: 24px 24px 0 24px;
  }
  
  .banner-menu {
    min-width: 180px;
  }
  
  .banner-text {
    left: 24px;
    top: 24px;
  }
  
  .banner-text h2 {
    font-size: 2rem;
  }
  
  .product-card,
  .category-card,
  .arrival-card,
  .explore-card {
    width: calc(33.33% - 16px);
    min-width: 200px;
  }
  
  .service-highlights-row {
    flex-wrap: wrap;
    gap: 24px;
  }
  
  .service-highlight {
    flex: 1;
    min-width: 280px;
  }
}

@media (max-width: 768px) {
  .container {
    padding: 0 16px;
  }
  
  .banner-section {
    flex-direction: column;
    padding: 16px 16px 0 16px;
  }
  
  .banner-menu {
    width: 100%;
    margin-bottom: 16px;
  }
  
  .banner-menu ul {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
  }
  
  .banner-menu li {
    padding: 8px 16px;
    background: #fff;
    border-radius: 6px;
    font-size: 15px;
  }
  
  .banner-text {
    left: 16px;
    top: 16px;
  }
  
  .banner-text h2 {
    font-size: 1.75rem;
  }
  
  .banner-text span {
    font-size: 16px;
  }
  
  .product-card,
  .category-card,
  .arrival-card,
  .explore-card {
    width: calc(50% - 12px);
    min-width: 160px;
    padding: 12px;
  }
  
  .product-title,
  .category-title,
  .arrival-title,
  .explore-title {
    font-size: 14px;
  }
  
  .product-price,
  .explore-price {
    font-size: 14px;
  }
  
  .add-to-cart,
  .explore-add-btn {
    font-size: 13px;
    padding: 6px 12px;
  }
  
  .service-highlights-row {
    flex-direction: column;
    gap: 16px;
  }
  
  .service-highlight {
    width: 100%;
    min-width: unset;
  }
  
  .service-title {
    font-size: 15px;
  }
  
  .service-desc {
    font-size: 13px;
  }
}

@media (max-width: 480px) {
  .container {
    padding: 0 12px;
  }
  
  .banner-section {
    padding: 12px 12px 0 12px;
  }
  
  .banner-menu li {
    padding: 6px 12px;
    font-size: 13px;
  }
  
  .banner-text h2 {
    font-size: 1.5rem;
  }
  
  .banner-text span {
    font-size: 14px;
  }
  
  .product-card,
  .category-card,
  .arrival-card,
  .explore-card {
    width: 100%;
    min-width: unset;
  }
  
  .section-header,
  .best-header,
  .category-header,
  .explore-header {
    padding-top: 16px;
    margin-bottom: 16px;
  }
  
  .section-badge,
  .category-badge {
    font-size: 12px;
    padding: 3px 8px;
  }
  
  h2, h3 {
    font-size: 1.25rem;
  }
  
  .view-all-btn {
    font-size: 13px;
    padding: 8px 20px;
  }
  
  .hero-banner-outer {
    padding: 16px 0;
  }
  
  .hero-banner-inner {
    flex-direction: column;
    gap: 16px;
  }
  
  .hero-banner-title {
    font-size: 1.5rem;
  }
  
  .hero-banner-buy-btn {
    font-size: 14px;
    padding: 10px 24px;
  }
} 