.footer {
  background: #111;
  color: #fff;
  padding: 64px 40px 0 40px;
  font-family: 'Poppins', Arial, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0;
}
.footer-content {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: flex-start;
  gap: 32px;
  margin-bottom: 32px;
}
.footer-section {
  min-width: 200px;
  max-width: 240px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  align-items: flex-start;
}
.footer-section h3, .footer-section h4 {
  margin-bottom: 16px;
  font-weight: bold;
  font-family: 'Poppins', Arial, sans-serif;
  font-size: 20px;
  line-height: 24px;
  text-align: left;
}
.footer-subscribe {
  margin-top: 8px;
}
.footer-subscribe-input {
  display: flex;
  margin-top: 8px;
  border: 1.5px solid #FAFAFA;
  border-radius: 4px;
  overflow: hidden;
  width: 100%;
}
.footer-subscribe-input input {
  padding: 8px;
  border: none;
  outline: none;
  width: 100%;
  font-family: 'Poppins', Arial, sans-serif;
  font-size: 16px;
  background: transparent;
  color: #fff;
}
.footer-subscribe-btn {
  background: none;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 12px;
  cursor: pointer;
}
.footer-subscribe-btn svg {
  display: block;
  stroke: #FAFAFA;
}
.footer-apps-row {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  margin-top: 8px;
}
.footer-qr {
  width: 80px;
  height: 80px;
  object-fit: cover;
  border-radius: 4px;
  background: #fff;
}
.footer-apps {
  display: flex;
  flex-direction: column;
  gap: 8px;
  justify-content: flex-start;
}
.footer-apps img {
  width: 90px;
  height: 32px;
  margin: 0;
  margin-bottom: 0;
  background: none;
  border-radius: 0;
  object-fit: contain;
}
.footer-socials {
  margin-top: 32px;
  display: flex;
  gap: 32px;
  justify-content: flex-start;
  align-items: center;
}
.footer-socials a {
  color: #fff;
  font-size: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer-bottom {
  border-top: 1px solid #333;
  text-align: center;
  padding: 16px 0;
  font-size: 14px;
  color: #aaa;
  margin-top: 16px;
  font-family: 'Poppins', Arial, sans-serif;
}

/* Tablet styles */
@media (max-width: 1024px) {
  .footer {
    padding: 48px 24px 0 24px;
  }
  
  .footer-content {
    gap: 24px;
  }
  
  .footer-section {
    min-width: 180px;
    max-width: 220px;
  }
  
  .footer-socials {
    gap: 24px;
  }
}

/* Mobile styles */
@media (max-width: 768px) {
  .footer {
    padding: 32px 16px 0 16px;
  }
  
  .footer-content {
    flex-direction: column;
    gap: 32px;
  }
  
  .footer-section {
    min-width: 100%;
    max-width: 100%;
    align-items: center;
    text-align: center;
  }
  
  .footer-section h3,
  .footer-section h4 {
    text-align: center;
  }
  
  .footer-subscribe-input {
    max-width: 300px;
    margin: 0 auto;
  }
  
  .footer-apps-row {
    justify-content: center;
  }
  
  .footer-socials {
    justify-content: center;
    gap: 24px;
  }
  
  .footer-bottom {
    padding: 12px 0;
    font-size: 12px;
  }
}

/* Small mobile styles */
@media (max-width: 480px) {
  .footer {
    padding: 24px 12px 0 12px;
  }
  
  .footer-content {
    gap: 24px;
  }
  
  .footer-section {
    gap: 8px;
  }
  
  .footer-section h3,
  .footer-section h4 {
    font-size: 18px;
    margin-bottom: 12px;
  }
  
  .footer-subscribe-input {
    max-width: 250px;
  }
  
  .footer-apps-row {
    flex-direction: column;
    align-items: center;
  }
  
  .footer-socials {
    gap: 16px;
  }
  
  .footer-socials a {
    font-size: 20px;
  }
} 