.product-detail-page {
  max-width: 1170px;
  margin: 0 auto;
  padding: 20px;
  font-family: 'Inter', sans-serif;
}

.breadcrumb {
  color: #000000;
  font-size: 14px;
  margin-bottom: 40px;
}

.breadcrumb span {
  opacity: 0.5;
}

.breadcrumb span.active {
  opacity: 1;
}

.product-main-section {
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 70px;
  margin-bottom: 60px;
}

.product-gallery {
  display: flex;
  gap: 30px;
}

.gallery-thumbs {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.gallery-thumbs img {
  width: 170px;
  height: 138px;
  object-fit: cover;
  border-radius: 4px;
  cursor: pointer;
  opacity: 0.5;
  transition: opacity 0.2s;
}

.gallery-thumbs img.active {
  opacity: 1;
}

.gallery-main img {
  width: 500px;
  height: 600px;
  object-fit: cover;
  border-radius: 4px;
}

.product-info {
  max-width: 400px;
}

.product-info h1 {
  font-size: 24px;
  font-weight: 600;
  margin: 0 0 16px;
  color: #000000;
}

.product-rating {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 16px;
}

.stars {
  color: #FFAD33;
  font-size: 20px;
}

.rating-number {
  color: #000000;
  opacity: 0.5;
}

.stock-status {
  color: #000000;
  opacity: 0.5;
}

.product-price {
  font-size: 24px;
  font-weight: 500;
  color: #000000;
  margin-bottom: 24px;
}

.product-description {
  color: #000000;
  opacity: 0.6;
  line-height: 1.5;
  margin-bottom: 24px;
}

.product-options {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.option-group {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.option-group label {
  font-weight: 500;
}

.color-options {
  display: flex;
  gap: 8px;
}

.color-btn {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  position: relative;
}

.color-btn.selected::after {
  content: '';
  position: absolute;
  inset: -4px;
  border: 2px solid #000000;
  border-radius: 50%;
}

.color-btn.black {
  background: #000000;
}

.color-btn.red {
  background: #DB4444;
}

.size-options {
  display: flex;
  gap: 8px;
}

.size-btn {
  padding: 8px 16px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  background: transparent;
  cursor: pointer;
  color: #000000;
  font-size: 14px;
}

.size-btn.selected {
  background: #000000;
  color: white;
}

.quantity-group {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-top: 8px;
}

.qty-btn {
  width: 40px;
  height: 40px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  background: transparent;
  border-radius: 4px;
  font-size: 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.qty-display {
  font-size: 16px;
  min-width: 24px;
  text-align: center;
}

.buy-now-btn {
  padding: 10px 48px;
  background: #DB4444;
  border: none;
  border-radius: 4px;
  color: white;
  font-weight: 500;
  cursor: pointer;
}

.wishlist-btn {
  width: 40px;
  height: 40px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  background: transparent;
  border-radius: 4px;
  font-size: 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #DB4444;
}

.delivery-info {
  margin-top: 40px;
  border: 1px solid #E4E7E9;
  border-radius: 4px;
}

.delivery-row {
  display: flex;
  gap: 16px;
  padding: 24px;
  border-bottom: 1px solid #E4E7E9;
}

.delivery-row:last-child {
  border-bottom: none;
}

.delivery-content {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.delivery-content h4 {
  margin: 0;
  font-size: 16px;
  font-weight: 500;
}

.delivery-link,
.delivery-text {
  color: #000000;
  opacity: 0.6;
  text-decoration: underline;
  font-size: 14px;
}

.details-link {
  color: #000000;
  text-decoration: underline;
}

.related-section {
  margin-top: 140px;
  margin-bottom: 60px;
}

.section-header {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 40px;
}

.red-indicator {
  width: 20px;
  height: 40px;
  background: #DB4444;
  border-radius: 4px;
}

.section-header h2 {
  font-size: 36px;
  font-weight: 500;
  color: #000000;
  margin: 0;
}

.related-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
}

.product-card {
  position: relative;
  background: white;
  border-radius: 4px;
  padding: 16px;
}

.product-card img {
  width: 100%;
  height: 250px;
  object-fit: cover;
  border-radius: 4px;
  margin-bottom: 16px;
}

.card-badge {
  position: absolute;
  top: 12px;
  left: 12px;
  background: #DB4444;
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: 500;
}

.wishlist-icon {
  position: absolute;
  top: 12px;
  right: 12px;
  width: 34px;
  height: 34px;
  border-radius: 50%;
  background: white;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  color: #DB4444;
  cursor: pointer;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15);
}

.card-content {
  padding: 8px 0;
}

.card-content h3 {
  font-size: 16px;
  font-weight: 500;
  color: #000000;
  margin: 0 0 8px;
}

.price-group {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 8px;
}

.current-price {
  color: #DB4444;
  font-weight: 500;
  font-size: 16px;
}

.original-price {
  color: #000000;
  opacity: 0.5;
  text-decoration: line-through;
  font-size: 16px;
}

.rating-group {
  display: flex;
  align-items: center;
  gap: 8px;
}

.rating-group .stars {
  color: #FFAD33;
  font-size: 16px;
}

.review-count {
  color: #000000;
  opacity: 0.5;
  font-size: 14px;
}

/* Tablet styles */
@media (max-width: 1024px) {
  .product-detail-page {
    padding: 16px;
  }
  
  .product-main-section {
    gap: 40px;
  }
  
  .gallery-thumbs img {
    width: 140px;
    height: 120px;
  }
  
  .gallery-main img {
    width: 400px;
    height: 500px;
  }
  
  .product-info {
    max-width: 350px;
  }
  
  .related-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* Mobile styles */
@media (max-width: 768px) {
  .product-detail-page {
    padding: 12px;
  }
  
  .product-main-section {
    grid-template-columns: 1fr;
    gap: 24px;
  }
  
  .product-gallery {
    flex-direction: column;
    gap: 16px;
  }
  
  .gallery-thumbs {
    flex-direction: row;
    overflow-x: auto;
    padding-bottom: 8px;
  }
  
  .gallery-thumbs img {
    width: 100px;
    height: 80px;
    flex-shrink: 0;
  }
  
  .gallery-main img {
    width: 100%;
    height: 400px;
  }
  
  .product-info {
    max-width: 100%;
  }
  
  .product-info h1 {
    font-size: 20px;
  }
  
  .product-price {
    font-size: 20px;
  }
  
  .product-description {
    font-size: 14px;
  }
  
  .option-group label {
    font-size: 14px;
  }
  
  .size-btn {
    padding: 6px 12px;
    font-size: 12px;
  }
  
  .quantity-group {
    flex-wrap: wrap;
  }
  
  .buy-now-btn {
    width: 100%;
    padding: 10px 24px;
  }
  
  .delivery-row {
    padding: 16px;
  }
  
  .delivery-content h4 {
    font-size: 14px;
  }
  
  .delivery-link,
  .delivery-text {
    font-size: 12px;
  }
  
  .related-grid {
    grid-template-columns: 1fr;
    gap: 16px;
  }
}

/* Small mobile styles */
@media (max-width: 480px) {
  .product-detail-page {
    padding: 8px;
  }
  
  .breadcrumb {
    font-size: 12px;
    margin-bottom: 24px;
  }
  
  .gallery-main img {
    height: 300px;
  }
  
  .gallery-thumbs img {
    width: 80px;
    height: 60px;
  }
  
  .product-info h1 {
    font-size: 18px;
  }
  
  .product-rating {
    gap: 4px;
  }
  
  .stars {
    font-size: 16px;
  }
  
  .rating-number,
  .stock-status {
    font-size: 12px;
  }
  
  .product-price {
    font-size: 18px;
  }
  
  .product-options {
    gap: 16px;
  }
  
  .option-group {
    gap: 8px;
  }
  
  .color-btn {
    width: 20px;
    height: 20px;
  }
  
  .qty-btn,
  .wishlist-btn {
    width: 32px;
    height: 32px;
    font-size: 16px;
  }
  
  .delivery-row {
    padding: 12px;
  }
  
  .delivery-row svg {
    width: 32px;
    height: 32px;
  }
  
  .section-header h2 {
    font-size: 18px;
  }
} 