.notfound-page {
  background: #fafafa;
  min-height: 100vh;
  padding-bottom: 40px;
}
.breadcrumb {
  padding: 32px 40px 0 40px;
  color: #888;
  font-size: 15px;
  margin-bottom: 16px;
}
.breadcrumb .active {
  color: #222;
  font-weight: 500;
}
.notfound-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 60vh;
  text-align: center;
}
.notfound-content h1 {
  font-size: 5rem;
  font-weight: bold;
  margin: 40px 0 16px 0;
}
.notfound-content p {
  font-size: 18px;
  color: #888;
  margin-bottom: 32px;
}
.back-home-btn {
  background: #db4444;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 14px 36px;
  font-size: 18px;
  font-weight: 500;
  cursor: pointer;
} 